<template>
	<div class="about">

		<div :span="4" v-if="!istoken">
			<el-result icon="warning" title="连接不可以" subTitle="">
				<template slot="extra">
					<el-button type="primary" @click="token">验证</el-button>
				</template>
			</el-result>
		</div>

		<el-row>
			<el-col :span="4">
				类型：<el-select v-model="imgtype.r18" placeholder="请选择">
					<el-option v-for="item in imgtypeoptions.r18" :key="item.value" :label="item.label"
						:value="item.value">
					</el-option>
				</el-select>
			</el-col>
			<el-col :span="4">
				标题：<el-input v-model="imgtype.keyword" placeholder="" style="width:200px"></el-input>
			</el-col>
			<el-col :span="4">
				标签：<el-input v-model="imgtype.tag" placeholder="" style="width:200px"></el-input>

			</el-col>
			<el-col :span="1" align="middle" style="padding: 10px">
				数量：
			</el-col>
			<el-col :span="4">
				<el-slider v-model="imgtype.num" style="width:200px"></el-slider>
			</el-col>
			<el-col :span="4">
				<el-button type="primary" @click="getApi">查询</el-button>
			</el-col>
		</el-row>

		<el-row :gutter="20">
			<el-col :span="6" v-for="(ocol,icol) in imgdate">
				<div class="grid-content bg-purple">
					<el-card :body-style="{ padding: '0px' }">
						<el-image :src="imgdate[icol].small" lazy>
						</el-image>
						<div style="padding: 14px;">
							<span>标题:{{imgdate[icol].title}}</span><br>
							<span>作者:{{imgdate[icol].author}}</span><br>
							<span>pid:{{imgdate[icol].pid}}</span><br>
							<div class="bottom clearfix">
								<time class="time">{{imgdate[icol].uploadDate}}</time>
								<el-button type="text" class="button" @click="lookimg(icol)">原图
								</el-button>
							</div>
						</div>
					</el-card>
				</div>
			</el-col>
		</el-row>


		<el-dialog title="原图" :visible.sync="dialogTableVisible" width='80%'>
			<el-image :src="originalurl">
			</el-image>
		</el-dialog>
	</div>
</template>


<script>
	export default {
		data() {
			return {

				imgtype: {
					r18: '0',
					num: 10,
					keyword: '',
					tag: '',
					size: 'original'
				},
				dialogTableVisible: false,
				elrownum: 0,
				imgtypeoptions: {
					r18: [{
						value: '0',
						label: '正常'
					}, {
						value: '1',
						label: '暴露'
					}, {
						value: '2',
						label: '随机'
					}],
					size: [{
						value: 'original',
						label: '原图'
					}, {
						value: 'regular',
						label: '普通'
					}, {
						value: 'small',
						label: '小图'
					}, {
						value: 'thumb',
						label: '小小图'
					}, {
						value: 'mini',
						label: '最小'
					}],
				},
				imgdate: [],
				originalurl: '',
				istoken: false
			}
		},
		beforeCreate() {
			document.title = '搜索'
		},
		mounted() {
			var other = this
			this.$http.get('https://8.134.127.32/setu/setu/lolicon', {
				params: {
					r18: other.imgtype.r18,
					num: other.imgtype.num,
					keyword: other.imgtype.keyword,
					tag: other.imgtype.tag,
				}
			}).then(function(response) {
				other.istoken = true
			}).catch(function(error) {
				other.istoken = false
			});
		},
		methods: {
			getApi: function() {
				var other = this;
				// console.log(other.imgtype)
				this.$http.get('https://8.134.127.32/setu/setu/lolicon', {
					params: {
						r18: other.imgtype.r18,
						num: other.imgtype.num,
						keyword: other.imgtype.keyword,
						tag: other.imgtype.tag,
					}
				}).then(function(response) {
					other.elrownum = Math.ceil(other.imgtype.num / 4)
					other.imgdate = response.data
				}).catch(function(error) {
					console.log(error);
				});
			},
			lookimg: function(i) {
				this.dialogTableVisible = true
				this.originalurl = this.imgdate[i].original
			},
			
			token: function() {
				var other = this
				this.$http.get('https://8.134.127.32/setu/setu/lolicon', {
					params: {
						r18: other.imgtype.r18,
						num: other.imgtype.num,
						keyword: other.imgtype.keyword,
						tag: other.imgtype.tag,
					}
				}).then(function(response) {
					other.istoken = true
				}).catch(function(error) {
					other.istoken = false
					window.open('https://8.134.127.32/', '', 'height=500,width=611,scrollbars=yes,status =yes')
				});
			}
		}
	}
</script>
